.container {
  background-color: #c0e1d6;
  min-height: 100vh;
}

.noitems {
  color: blue;
  text-align: center;
  padding: 2rem;
}

.total {
  max-width: 36rem;
  margin: 0 auto;
  color: blue;
  text-align: right;
  padding: 2rem 0;
}

.order {
  max-width: 36rem;
  margin: 0 auto;
  color: blue;
  text-align: right;
  padding: 2rem 0;
}

.order button,
.clear button {
  font-size: 1.2rem;
  padding: 0.5rem 1rem;
  width: 10rem;
  border-radius: 6px;
}

.order button:hover,
.clear button:hover {
  cursor: pointer;
  /* background-color: blue; */
  background-color: grey;
}

.clear {
  max-width: 36rem;
  margin: 0 auto;
  color: blue;
  text-align: right;
  padding: 2rem 0;
}

@media (max-width: 600px) {
  .total {
    padding-right: 0.5rem;
    font-size: 1.5rem;
  }

  .order,
  .clear {
    padding-right: 0.5rem;
  }
}
