.error-modal {
  text-align: center;
  position: fixed;
  top: 30vh;
  left: calc(50% - 15rem);
  max-width: 30rem;
  background: white;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
  z-index: 100;
  border-radius: 7px;
}

.error-modal h2 {
  margin: 0;
  padding: 1rem;
  background: #ff2058;
  color: white;
  border-radius: 7px 7px 0 0;
}

.error-modal p {
  padding: 1rem;
}

.error-modal__actions {
  display: flex;
  justify-content: center;
  padding: 1rem;
}

.error-modal__actions button {
  background: #ff2058;
  color: white;
  padding: 0.5rem 1rem;
  border-radius: 6px;
  cursor: pointer;
}

.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background: rgba(0, 0, 0, 0.75);
  z-index: 50;
}

@media (max-width: 600px) {
  .error-modal {
    left: unset;
    margin: 1rem;
  }
}
